.testimonial {
    .slick-slide {}

    .item {
        margin: 14px;
        margin-bottom: 20px;
        box-shadow: $box-shadow;
        background: #fff;
        border-radius: 10px;
        padding: 30px;
        cursor: pointer;

        &:hover {
            -webkit-backdrop-filter: blur(50px);
            backdrop-filter: blur(50px);
            background: $skyblue-color;
            transition: all 0.8s ease 0s;

            p,
            h2,
            h5 {
                color: $white-color;
            }
        }

        .item-img {
            p {
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 4;
                overflow: hidden;
                font-weight: 300;
                margin-bottom: 30px;
                color: $gray-color;
            }

            .linkedin {
                width: 30px;
                margin: 0 auto 20px;

            }

            .profileImage {
                width: 100px;
                border-radius: 50%;
                height: 100px;
                object-fit: cover;
                margin: auto;
                margin-bottom: 20px;
            }

            h2 {
                font-family: $heading-font;
                margin-bottom: 4px;
                font-weight: 300;

                @media only screen and (max-width: 1199px) {
                    font-size: 22px;
                    margin-bottom: 0;
                }
            }

            h5 {
                margin: 0;
                font-size: 14px;
                letter-spacing: 2px;
                opacity: 0.5;
                font-weight: 400;
            }
        }

        @media only screen and (max-width: 480px) {
            padding: 30px 25px 20px;
            margin: 5px;
            box-shadow: 2px 2px 2px rgba(152, 193, 217, 49%);
        }
    }
}