.projects {
    .highlights-inner {
        padding-top: 30px;

        .item {
            width: 100%;
            // margin-bottom: 30px;
            margin-bottom: 50px;
            align-items: center;

            &:last-child {
                margin-bottom: 0;
            }

            .project-details {
                // width: calc(100% - 684px);
                width: calc(100% - 800px);
                // padding: 15px;
                padding: 74px;
                text-align: left;

                @media only screen and (max-width: 1100px) {
                    width: 100%;
                    padding: 20px 0 0;
                }

                .project-title {
                    position: relative;
                    font-family: $heading-font;
                    font-weight: 600;
                    margin-bottom: 34px;

                    &::after {
                        position: absolute;
                        content: ' ';
                        width: 40px;
                        height: 3px;
                        background: $skyblue-color;
                        left: 0;
                        bottom: -10px;
                    }

                }

                ul {
                    li {
                        font-weight: 300;
                        position: relative;

                        &::after {
                            content: " ";
                            background-color: $skyblue-color;
                            width: 12px;
                            height: 12px;
                            position: absolute;
                            border-radius: 50%;
                            left: -20px;
                            top: 7px;
                        }

                    }

                    a {
                        margin-top: 20px;
                    }
                }
            }

        }
    }
}

.screen {
    display: block;
    // width: 670px;
    width: 800px;
    // height: 320px;
    height: 400px;
    overflow: hidden;
    position: relative;
    border-radius: 10px;
    border: 1px solid rgb(0 0 0 / 0.1);
    box-shadow: $box-shadow;
}

.screen img {
    // bottom: -1210px;
    // bottom: -1362px;
    width: 100%;
    height: auto;
    position: absolute;
    z-index: 0;
    margin: 0;
    padding: 0;
    left: 0;
    -webkit-transition: top 11s;
    -moz-transition: top 11s;
    -ms-transition: top 11s;
    -o-transition: top 11s;
    transition: bottom 11s;

    @media only screen and (max-width: 1100px) {
        top: 0;

    }

    // @media only screen and (max-width: 1360px) {
